@import url(https://fonts.googleapis.com/css2?family=Antic+Slab&family=Anton&family=Limelight&family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    /* outline: 1px dotted green; */
}

:root {
    --color1: #1A2B34;
    --color2: #6498CE;
    --color3: #FBC95A;
    --color4: #D0824E;

    --font-h1: 'Limelight', cursive;
    --font-h2: 'Antic Slab', serif;
    --font-p: 'Quicksand', sans-serif;
    --font-em: 'Anton', sans-serif;
}

h1{
    font-family: 'Limelight', cursive;
    font-family: var(--font-h1);
}

h2 {
    font-family: 'Antic Slab', serif;
    font-family: var(--font-h2);
}

p{
    font-family: 'Quicksand', sans-serif;
    font-family: var(--font-p);
}

a {
    color: black;
    text-decoration: none;
}

body {
    height: auto;
    padding-bottom: 5rem;
    background-image: url(/static/media/background.759859ea.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
}

.NavBar ul{
    position: fixed;
    overflow: hidden;
    bottom: 0;
    width: 100%;
    padding-bottom: 1rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    background: #6498CE;
    background: var(--color2);
    border-top: 1px solid black;
}

.NavBar li{
    display: flex;
    justify-content: center;
    width: 3rem;
    text-align: center;
    padding: 1rem;
    background: #D0824E;
    background: var(--color4);
    border-radius: 0 0 10px 10px;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
}

.Nav-Active li{
    background: #FBC95A;
    background: var(--color3);
}

.content {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

.box {
    margin: 1rem 0;
    display: flex;
    flex-flow: column nowrap;
    width: 80%;
    padding: 1rem;
    background: white;
    border-radius: 10px;
    text-align: left;
}

.title {
    color: #6498CE;
    color: var(--color2);
}

.imgSquare {
    display: flex;
    align-self: center;
    height: 200px;
    width: 200px;
}

.coolguyEmoji {
    height: 20px;
}

