@media screen and (min-width:700px){
    h1 {
        color: var(--color4);
    }
    .wrapper{
        margin-top: 10%;
    }
    .content{
        flex-flow: row wrap;
        margin: 2rem;
        padding: 1rem;
        background: white;
        border-radius: 2rem;
    }

    .box {
        width: 100%;
    }

    .box + .box {
        width: 50%;
        flex-grow: 1;
    }
    .meandkendal{
        width: 200px;
    }
}
@media screen and (min-width:1024px){
    .wrapper{
        margin-top: 7%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
    }
    
    .routerWrapper {
        width: 75vw;
        height: 75vh;
    }
    .content {
        margin: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
    }

    .content::-webkit-scrollbar {
        display: none;
    }

    .NavBar ul{
        margin: 0;
        padding: 0;
        position: relative;
        display: flex;
        flex-flow: column;
        height: 100%;
        background: none;
        border-top: 0;
        border-right: 1px solid black;
    }

    .NavBar li {
        height: 4rem;
        margin-top: 1rem;
        border-top: 1px solid black;
        border-bottom: 1px solid black;
        border-left: 1px solid black;
        border-right: 0;
        border-radius: 10px 0 0 10px;
    }

    .box {
        display: flex;
    }

    .box + .box{
        flex-grow: 1;
        margin: 1rem;
        max-height: 400px;
        max-width: 400px;
        overflow: auto;
        scroll-behavior: auto;
        background: var(--color3);
        box-shadow: 4px 4px 4px black;
    }

    .box::-webkit-scrollbar {
        display: none;
    }

    .fullscreen {
        max-width: 100% !important;
        max-height: 100% !important;
    }
}

.link-icons img {
